import VueRouter from 'vue-router'
import Vue from 'vue'
Vue.use(VueRouter)
const router = new VueRouter({
    routes:[
        {
            path:'/',
            redirect:'/home'
        },
        {
            name:'gonxueyun',
            path:'/gonxueyun',
            component:() => import("@/pages/docs/gonxueyun/gonxueyun"),
            meta:{title:'工学云/蘑菇丁自动打卡'}
        },
        {
            name:'home',
            path:'/home',
            component:() => import("@/pages/home/home.vue")
        },
        {
            name:'tool',
            path:'/tool',
            component: () => import("@/pages/tool/tool"),
            meta:{title:'Tool'}
        },
        {
            name:'author',
            path:'/author',
            component:() => import('@/pages/info/info'),
            meta:{title:'Author'}
        },
        {
            name:'proxy_pool',
            path:'/proxy_pool',
            component:() => import("@/pages/docs/proxy_pool/proxy_pool"),
            meta:{title:'工学云/蘑菇丁利用代理打卡'}
        },
        {
            name:'teachermate',
            path:'/teachermate',
            component: () => import("@/pages/docs/teachermate/teachermate"),
            meta:{title:'微助教打卡(不是全自动)'}
        },
        {
            name:'break',
            path:'/break',
            component: () => import('@/pages/docs/break/break'),
            meta:{title:'破解某锁机软件'}
        },
        {
            name:'breakvpn',
            path:'/breakvpn',
            component: () => import("@/pages/docs/breakvpn/breakvpn"),
            meta:{title:'破解某vpn的vip'}
        },
        {
            name:'xxs',
            path:'/xxs',
            component:() => import("@/pages/docs/xxs/xxs")
        }
    ],
    //默认路由跳转时滚到顶部
    scrollBehavior (to, from, savedPosition) {
            return { y:0 }
    }
})
router.beforeEach((to,from,next)=>{
    document.title = to.meta.title || 'breakme.tk'
    next()
})
export default router